import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SharedModule } from '../shared/shared.module';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { NgxMaskModule } from '../shared/ngx-mask';
import { RoutingModule } from './faleConosco.routing.module';

import { Modal } from './modal/modal.components';
import { FaleConoscoComponent } from './faleConosco.component';
import { ModalAndamento } from './modal-andamento/modal-andamento.components';
import { FileUploadModule } from 'ng2-file-upload';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    RoutingModule,
    ReactiveFormsModule,
    FileUploadModule, 
    SharedModule,
    NgbModule,
    NgxMaskModule.forRoot(),
  ],
  declarations: [FaleConoscoComponent, Modal, ModalAndamento],
  entryComponents: [ Modal, ModalAndamento ]
  
  
})
export class FaleConoscoModule { }



