import { Modal } from './modal/modal.components';
import { RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { FaleConoscoComponent } from './faleConosco.component';
import { FaleConoscoGuard } from '../guards/faleConosco.guard';

const moduloRoutes = [
    {path: '', component: FaleConoscoComponent, 
     canActivateChild: [FaleConoscoGuard],
     children : [
        {path: 'novo', component: Modal}
    ]}
];
@NgModule({
    imports: [RouterModule.forChild(moduloRoutes)],
    exports: [RouterModule]
})
export class RoutingModule {}

