import { Alert } from 'selenium-webdriver';
import { Cidade } from './../../shared/models/cidade';
import { EstadoBr } from './../../shared/models/estado-br.model';
import { DropdownService } from './../../shared/services/dropdown.service';
import { ConsultaCepService } from './../../shared/services/consulta-cep.service';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Pais } from './../../shared/models/pais';
import { cepValidator, getLabelAllFields, getFormValidationErrors } from '../../shared/form-validation';
import { map, tap, distinctUntilChanged, switchMap } from 'rxjs/operators';
import { empty } from 'rxjs';
import { Service } from '../services/service';


@Component({
    selector: 'app-modal',
    templateUrl: 'modal.html',
    styleUrls: ['modal.css']
})
export class Modal implements OnInit {
    @Input() public formPai;
    @Output() passEntry: EventEmitter<any> = new EventEmitter();

    form: FormGroup;
    submitted = false;
    alerts = [];

    paises: Pais[];
    estados: EstadoBr[];
    cidades: Cidade[];
    cidadeTemp = "";
    carregando = true;
    ocultarDadosPessoais = false;
    ocultarEndereco = false;
    ocultarDocumentosPessoais = false;
    listaTipos: any = [{ codigo: '1', descricao: 'Informação' }, { codigo: '2', descricao: 'Solicitação' }, { codigo: '3', descricao: 'Reclamação' }, { codigo: '4', descricao: 'Agendamento' }];
    modoLeitura = true;
    
    constructor(
        public activeModal: NgbActiveModal,
        private fb: FormBuilder,
        private service: Service,
        private cepService: ConsultaCepService,
        private dropdownService: DropdownService,

    ) { }

    ngOnInit() {

        this.form = this.fb.group({

            nome: [null, [Validators.required, Validators.minLength(3), Validators.maxLength(100)]],
            email: [null, [Validators.required, Validators.email]],
            tipo: [null],
            mensagem: [null],
  
            cpf: [null],
            celular: [null],
       
             cep: [null, [Validators.required, cepValidator]],
             complemento: [null],
             logradouro: [null, Validators.required],
             bairro: [null, Validators.required],
             cidade: [null, Validators.required],
             uf: [null, Validators.required]
          
        })

        this.dropdownService.getPaises().subscribe(dados =>
            {
                this.paises = dados;
                this.dropdownService.getEstadosBr().subscribe(dados => 
                    {
                        this.estados = dados;

                        //CARREGA OS DADOS DO MODAL
                        for(var key in this.formPai){
                            if(this.formPai.hasOwnProperty(key)) {

                                if (this.form.get( key ) == null){
                                    console.info('Campo ' + key + ' não definido no formulário');
                                }else{
                                    console.info('Campo ' + key + ' definido');
                                    this.form.get( key ).setValue(this.formPai[key]);
                                    if (key.indexOf("data") > -1){
                                        console.info('Campo ' + key + this.formPai[key]); 
                                        let dataSemFormatacao = this.formPai[key];
                                        let ano = dataSemFormatacao.substr(0,4);
                                        let mes = dataSemFormatacao.substr(5,2);                                        
                                        let dia = dataSemFormatacao.substr(8,2);                                        
                                        console.info('Campo sem formato ' +"|" +  dia +"|" + mes +"|" + ano);                                         
                                        this.form.get( key ).setValue(dia + mes + ano);
                                    }
                                }
                            }
                        }
                        this.carregando = false;                                
                    });
        });
        
        this.form.get('uf').valueChanges
            .pipe(
                tap(uf => console.log('Novo estado: ', uf)),
                map(uf => this.estados.filter(e => e.sigla === uf)),
                map(estados => estados && estados.length > 0 ? estados[0].id : empty()),
                switchMap((estadoId: number) => this.dropdownService.getCidades(estadoId)),
            tap(console.log)
        ).subscribe(cidades => this.cidades = cidades);
            
        this.form.get('cep').statusChanges
        .pipe(
          distinctUntilChanged(),
          tap(value => console.log('status CEP:', value)),
          switchMap(status => status === 'VALID' ?
            this.cepService.consultaCEP(this.form.get('cep').value)
            : empty()
          )
        )
        .subscribe(dados => dados ? this.populaDadosForm(dados) : {});
    }

    sair(){
        this.activeModal.close('FECHAR');
    }

    populaDadosForm(dados) {
        // this.formulario.setValue({});

        this.form.patchValue({
            uf: dados.uf,
            cidade: dados.localidade,
            logradouro: dados.logradouro,
            complemento: dados.complemento,
            bairro: dados.bairro,
        });

    }

    resetaDadosEnderecoForm() {
        this.form.patchValue({
            rua: null,
            complemento: null,
            bairro: null,
            cidade: null,
            uf: null
        });
    }


    close(alert: Alert) {
        this.alerts.splice(this.alerts.indexOf(alert), 1);
    }

    getLabelField(fieldName: string) {
        return getLabelAllFields(fieldName);
    }

    validarData(control: AbstractControl): { [key: string]: any } | null {
        const v = control.value;
        if (null != v && v.length == 8) {
            const data = v.substr(0, 2) + "/" + v.substr(2, 2) + "/" + v.substr(4, 4);
            const patternData = /^(((0[1-9]|[12][0-9]|3[01])([-.\/])(0[13578]|10|12)([-.\/])(\d{4}))|(([0][1-9]|[12][0-9]|30)([-.\/])(0[469]|11)([-.\/])(\d{4}))|((0[1-9]|1[0-9]|2[0-8])([-.\/])(02)([-.\/])(\d{4}))|((29)(\.|-|\/)(02)([-.\/])([02468][048]00))|((29)([-.\/])(02)([-.\/])([13579][26]00))|((29)([-.\/])(02)([-.\/])([0-9][0-9][0][48]))|((29)([-.\/])(02)([-.\/])([0-9][0-9][2468][048]))|((29)([-.\/])(02)([-.\/])([0-9][0-9][13579][26])))$/;
            if (!patternData.test(data)) {
                return { dataInvalida: true };
            }
        }
    }
    

    salvar() {
        console.log('Salvando Estudante');
        this.alerts = [];
        this.submitted = true;
    
        // stop here if form is invalid
        if (this.form.invalid) {
   
          this.alerts = getFormValidationErrors(this.form.controls);
    
          console.log('Formulário inválido');
          return;
        }
        this.carregando = true;

        this.service.salvar(this.form)
          .subscribe(
            response => {
              console.log("Status" + response.status);          
              if (response.status.codigo == 0){
                this.alerts = Array.from([{ type: 'success', message: response.status.mensagem }])
              }else{
                this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
              }
            this.carregando = false;
            },
            err => {
              this.carregando = false;
              this.alerts = Array.from([{ type: 'danger', message: err.message }])          
              console.log(err);
            });
    }

    show(secao){
        if (secao == 'ocultarDadosPessoais'){
            if (this.ocultarDadosPessoais){
                this.ocultarDadosPessoais = false;
            }else{
                this.ocultarDadosPessoais = true;
            }    
        } 
        if (secao == 'ocultarEndereco'){
            if (this.ocultarEndereco){
                this.ocultarEndereco = false;
            }else{
                this.ocultarEndereco = true;
            }    
        } 

        if (secao == 'ocultarDocumentosPessoais'){
            if (this.ocultarDocumentosPessoais){
                this.ocultarDocumentosPessoais = false;
            }else{
                this.ocultarDocumentosPessoais = true;
            }    
        } 

    }
}