import { Alert } from 'selenium-webdriver';
import { Cidade } from './../../shared/models/cidade';
import { EstadoBr } from './../../shared/models/estado-br.model';
import { DropdownService } from './../../shared/services/dropdown.service';
import { ConsultaCepService } from './../../shared/services/consulta-cep.service';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Pais } from './../../shared/models/pais';
import { cepValidator, getLabelAllFields, getFormValidationErrors } from '../../shared/form-validation';
import { map, tap, distinctUntilChanged, switchMap } from 'rxjs/operators';
import { empty, Observable } from 'rxjs';
import { Service } from '../services/service';
import { FileUploader } from 'ng2-file-upload';
import { environment } from '../../../environments/environment';
import { HttpClient } from '@angular/common/http';

const URL_UPLOAD = `${environment.API}faleConosco/uploadFile.php`;;

@Component({
    selector: 'app-modal-andamento',
    templateUrl: 'modal-andamento.html',
    styleUrls: ['modal-andamento.css']
})
export class ModalAndamento implements OnInit {
    @Input() public formPai: any;
    @Output() passEntry: EventEmitter<any> = new EventEmitter();

    uploadForm: FormGroup;

    form: FormGroup;
    
    submitted = false;
    subscription = null;
    alerts = [];
    carregando = false;
    ocultarFormulario = true;
    desabilitarBotaoSalvar = false;

    listaSMS: any = [{ codigo: 'Sim', descricao: 'Sim' }, { codigo: 'Não', descricao: 'Não' }];
    listaStatus: any = [{ codigo: '0', descricao: 'Pendente' }, { codigo: '1', descricao: 'Aguardando parecer' }, { codigo: '2', descricao: 'Concluído' }, { codigo: '3', descricao: 'Cancelado' }];
    listaAndamentos =[];

    public uploader: FileUploader = new FileUploader({ url: URL_UPLOAD });

    

    constructor(
        public activeModal: NgbActiveModal,
        private fb: FormBuilder,
        private service: Service,
        private cepService: ConsultaCepService,
        private dropdownService: DropdownService,
        private http: HttpClient

    ) { }

    ngOnInit() {

        this.form = this.fb.group({
            id_pai: [null],
            id: [null ],
            data: [null, [this.validarData]],
            status: [null, [Validators.required]],
            sms: [null],
            descricao: [null],
            nomeAnexos: [null],            
            nome: [null],
            email: [null],
            celular: [null],
            usuario:[""],


        })

        this.uploadForm = this.fb.group({
          document: [null, null],
          type: [null, Validators.compose([Validators.required])]
      });       

        var jsonUsuario = JSON.parse(localStorage.getItem("usuario"));
        console.log(jsonUsuario.nome);


        this.form.get('id_pai').setValue(this.formPai.id);        
        this.form.get('nome').setValue(this.formPai.nome);
        this.form.get('email').setValue(this.formPai.email);
        this.form.get('celular').setValue(this.formPai.celualar);
        this.form.get('usuario').setValue(jsonUsuario.nome);



        
        this.subscription = this.service.getAndamentos(this.formPai.id)
        .subscribe(
          response => {
            console.log("Status" + response.status);
            if (response.status.codigo == 0) {
              this.listaAndamentos = response.dados;
            } else {
              this.listaAndamentos = [];
              this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
            }
            this.carregando = false;
          },
          err => {
            this.carregando = false;
            this.alerts = Array.from([{ type: 'danger', message: err.message }])
            console.log(err);
          });     
          
           

    }

   novo(){
       this.ocultarFormulario = false;
       this.desabilitarBotaoSalvar = false;

       var data = new Date(),
        dia  = data.getDate().toString(),
        diaF = (dia.length == 1) ? '0'+dia : dia,
        mes  = (data.getMonth()+1).toString(), //+1 pois no getMonth Janeiro começa com zero.
        mesF = (mes.length == 1) ? '0'+mes : mes,
        anoF = data.getFullYear();
       this.form.get('data').setValue(diaF+"/"+mesF+"/"+anoF);    
       this.form.get('status').setValue("");       
       this.form.get('sms').setValue("");       
       this.form.get('descricao').setValue("");       
       this.form.get('id').setValue("");       


       this.uploadForm = this.fb.group({
        document: [null, null],
        type: [null, Validators.compose([Validators.required])]
    });       


       
   }

   cancelar(){
    this.ocultarFormulario = true;
    }

    sair(){
        this.ocultarFormulario = true;
    }

    excluir(andamento){
        if (confirm('Confirma a exclusão deste andamento ?')){
            this.service.excluirAndamento(andamento)
            .subscribe(
              response => {
                console.log("Status" + response.status);          
                if (response.status.codigo == 0){
                  this.alerts = Array.from([{ type: 'success', message: response.status.mensagem }])
                  this.formPai = response.dados;
                  console.log('AAA' + response.dados);
                  this.listaAndamentos = response.dados;                                  
                  this.ocultarFormulario = true;                
                }else{
                  this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
                  this.desabilitarBotaoSalvar = false;          
                }
              this.carregando = false;
              },
              err => {
                this.carregando = false;
                this.alerts = Array.from([{ type: 'danger', message: err.message }])          
                console.log(err);
              });
  
        }

    }
    

    close(alert: Alert) {
        this.alerts.splice(this.alerts.indexOf(alert), 1);
    }

    getLabelField(fieldName: string) {
        return getLabelAllFields(fieldName);
    }

    validarData(control: AbstractControl): { [key: string]: any } | null {
        const v = control.value;
        if (null != v && v.length == 8) {
            const data = v.substr(0, 2) + "/" + v.substr(2, 2) + "/" + v.substr(4, 4);
            const patternData = /^(((0[1-9]|[12][0-9]|3[01])([-.\/])(0[13578]|10|12)([-.\/])(\d{4}))|(([0][1-9]|[12][0-9]|30)([-.\/])(0[469]|11)([-.\/])(\d{4}))|((0[1-9]|1[0-9]|2[0-8])([-.\/])(02)([-.\/])(\d{4}))|((29)(\.|-|\/)(02)([-.\/])([02468][048]00))|((29)([-.\/])(02)([-.\/])([13579][26]00))|((29)([-.\/])(02)([-.\/])([0-9][0-9][0][48]))|((29)([-.\/])(02)([-.\/])([0-9][0-9][2468][048]))|((29)([-.\/])(02)([-.\/])([0-9][0-9][13579][26])))$/;
            if (!patternData.test(data)) {
                return { dataInvalida: true };
            }
        }
    }
    

    salvar() {
        console.log('Salvando Andamento');
        this.alerts = [];
        this.submitted = true;
        this.desabilitarBotaoSalvar = true;

        var nomeAnexos = "";
        for (let i = 0; i < this.uploader.queue.length; i++) {
          let fileItem = this.uploader.queue[i]._file;
          const el: HTMLInputElement = <HTMLInputElement>document.getElementById("uploadArquivos");
          const anexoArquivos = el.value;
          if (anexoArquivos.indexOf(fileItem.name) > -1) {
            nomeAnexos = nomeAnexos + fileItem.name + "|";
          }
        }

        this.form.get('nomeAnexos').setValue(nomeAnexos);
    
        // stop here if form is invalid
        if (this.form.invalid) {
   
          this.alerts = getFormValidationErrors(this.form.controls);
    
          console.log('Formulário inválido');
          this.desabilitarBotaoSalvar = false;          
          return;
        }
        this.carregando = true;

        this.service.salvarAndamento(this.form)
          .subscribe(
            response => {
              console.log("Status" + response.status);          
              if (response.status.codigo == 0){
                this.alerts = Array.from([{ type: 'success', message: response.status.mensagem }])
                this.uploadFiles(this.form.get('id_pai').value + "-" + response.id);
                this.listaAndamentos = response.dados;                
                this.ocultarFormulario = true;                
              }else{
                this.alerts = Array.from([{ type: 'danger', message: response.status.mensagem }])
                this.desabilitarBotaoSalvar = false;          
              }
            this.carregando = false;
            },
            err => {
              this.carregando = false;
              this.alerts = Array.from([{ type: 'danger', message: err.message }])          
              console.log(err);
            });
    }



    uploadFiles(id: string ) {
      for (let i = 0; i < this.uploader.queue.length; i++) {
          let fileItem = this.uploader.queue[i]._file;
          if (fileItem.size > 10000000) {
              alert("Each File should be less than 10 MB of size.");
              return;
          }
      }
      for (let j = 0; j < this.uploader.queue.length; j++) {
          let tipo = "";
          let data = new FormData();
          let fileItem = this.uploader.queue[j]._file;

          console.info(this.uploader.queue[j]);
          console.log(fileItem.name);
          data.append('file', fileItem);
          data.append('id', id);
          data.append('fileName', fileItem.name);
          data.append('fileSeq', 'seq' + j);
          data.append('dataType', this.uploadForm.controls.type.value);
          this.uploadFile(data).subscribe(data => console.log(data.status.mensagem));
      }
//        this.uploader.clearQueue();
  }

  uploadFile(data: FormData): Observable<any> {
    return this.http.post(URL_UPLOAD, data);
  }
  
  listarAnexos(id, a){
    var htmlRetorno = "<ul>";
    var listaAnexos = a.split("|");
    for(var i =0; i < listaAnexos.length; i++){
      if (listaAnexos[i] != ""){
        htmlRetorno = htmlRetorno + "<ul><li><a href='https://anexo.crago.org.br/" + id +"-"+ listaAnexos[i] + "' target='blank'>"+ listaAnexos[i] +"</a></li></ul>"  ;
      }
    }
    htmlRetorno = htmlRetorno + "</ul>";
    return htmlRetorno;    
}
    
}